.lightbox {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  visibility: hidden;
  opacity: 0;
}
.lightbox.lightbox_open {
  z-index: 10;
  visibility: visible;
  opacity: 1;
}
.lightbox.lightbox_open .lightbox_content {
  opacity: 1;
}
.lightbox.lightbox_open .lightbox_overlay {
  opacity: 1;
}
.lightbox.lightbox_open .container_lightbox_content {
  opacity: 1;
  animation-name: anim-open;
  animation-timing-function: cubic-bezier(0.6, 0, 0.4, 1);
  animation-duration: 0.4s;
  animation-fill-mode: forwards;
}
.lightbox.lightbox_close .container_lightbox_content {
  animation-name: anim-close;
  animation-timing-function: cubic-bezier(0.6, 0, 0.4, 1);
  animation-duration: 0.4s;
  animation-fill-mode: forwards;
}
.lightbox .lightbox_overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0;
  transition: 0.4s opacity linear;
}
.lightbox .lightbox_content {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1500;
  width: 100%;
  max-width: 80%;
  margin: 0 auto;
  opacity: 0;
  transform: translate(-50%, -50%);
  transition: 0.4s opacity linear;
}
.lightbox .container_lightbox_content {
  padding: 0;
  text-align: center;
  opacity: 0;
}
.lightbox .container_lightbox_content img {
  width: 100%;
  max-width: 100%;
}
.lightbox .lightbox_interaction {
  text-align: center;
}
.lightbox .wrapper {
  position: relative;
  display: inline-block;
  max-width: 340px;
}
.lightbox .previous {
  position: absolute;
  top: 0;
  left: 0;
  padding: 6px 12px;
  text-transform: uppercase;
  background-color: #bba3d0;
  color: #ffffff;
  cursor: pointer;
}
.lightbox .previous.button_hidden {
  display: none;
}
.lightbox .image_comment {
  display: block;
  max-width: 100%;
  margin-top: 10px;
  text-transform: uppercase;
  color: #ffffff;
}
.lightbox .next {
  position: absolute;
  top: 0;
  right: 0;
  padding: 6px 12px;
  text-transform: uppercase;
  background-color: #bba3d0;
  color: #ffffff;
  cursor: pointer;
}
.lightbox .next.button_hidden {
  display: none;
}
.lightbox .lightbox_x_close {
  display: inline-block;
  margin: 0 auto 10px;
  padding: 6px 12px;
  text-transform: uppercase;
  background-color: #a6a6a6;
  color: #ffffff;
  cursor: pointer;
}

@keyframes anim-open {
  0% {
    transform: scale3d(0, 0, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}
@keyframes anim-close {
  100% {
    transform: scale3d(0.5, 0.5, 1);
  }
}
